
// CONECTAR HTML CON JS
document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
})

// //CREAMOS LA FUNCION IniciarApp

function iniciarApp() {
    crearGaleria()
    scrolNav()
    navegacionFija ()
}
//FUNCION PARA DEJAR NUESTRA BARRA DE NAVEGACION FIJA
function navegacionFija () {
    const barraFija = document.querySelector('.header')
    const sobreFestival =document.querySelector('.contenido-festival')
    const barraBody = document.querySelector('body')
    window.addEventListener('scroll', function(){
        if(sobreFestival.getBoundingClientRect().top < 0) {
            barraFija.classList.add('fijo');
            barraBody.classList.add('barra-fija')
        }else {
            barraFija.classList.remove('fijo')
            barraBody.classList.remove('barra-fija')
    }       
    })
}

//CREAMOS LA FUNCION PARA DAR BIEN EL SCROLL
function scrolNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a')
    enlaces.forEach(enlace => {
    enlace.addEventListener('click', function(e){
        const seccionScroll = e.target.attributes.href.valiu
        const seccion = document.querySelector(seccionScroll)
    })
    });
}

// CREANDO LA FUNCION crearGaleria

function crearGaleria(){
    
    const galeria= document.querySelector('.imagines-galeria'); //genaramos una variable y ponermos donde se ubica en el html
    for(let i=1 ;i<=12 ;i++){   // utilizamos un bucle para todas las imagenes que tenemos
        const imagen = document.createElement('picture'); // en cada i se crea un <picture> de html
        imagen.innerHTML =`  
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">  
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen galeria">
        `;   //para pegar lo que tendrai que ir en el picture
        imagen.onclick = function(){  // .onclik creamos un evento para mostrar otra imagen cuando damos click
            mostrarImagen(i);  //la funcion que creamos para que se realice el evento cuando hacemo click en la imagen
        }
        
        galeria.appendChild(imagen); //para que apezca en nuestro proyeto
    }

};

//FUNCION QUE VAMOS A LLAMAR CUANDO HAGAMOS CLICK EN CUALQUIER IMAGEN
function mostrarImagen(id) {
    const imagen = document.createElement('picture'); // en cada i se crea un <picture> de html
    imagen.innerHTML =`  
    <source srcset="build/img/grande/${id}.avif" type="image/avif">  
    <source srcset="build/img/grande/${id}.webp" type="image/webp">
    <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen galeria">
    `;
    

    // CREANDO EL OVERLAY 
    const overlay = document.createElement('DIV'); //Como la imagen se genera en otra caja, se crea el div
    overlay.appendChild(imagen);  // para llamar el div
    overlay.classList.add('overlay');  // crear la clase para dar estilo con css
    overlay.onclick = function() {     // este onclick es para cerrar el overlay clikando en cualquier parte no solo en la x
        const body = document.querySelector('body');
        body.classList.remove('fijar-body')
        overlay.remove();

    }

    // CREANDO EL BOTON DE CERRAR Y DESACTIVAR EL SCROLL, MIENTRAS ESTE LA IMAGEN EN PANTALLA
    const cerrarFoto = document.createElement('P');
    cerrarFoto.textContent ="X";
    cerrarFoto.classList.add('btn-cerrar');
    cerrarFoto.onclick= function() {  // onclick para que haga algo al apretar al x
        const body= document.querySelector('body');   // para que no haga scroll en todo el body
        body.classList.remove('fijar-body');           // con remove se elimina esa clase 
        overlay.remove();               // con remove, se elimina el overlay    
    }
    overlay.appendChild(cerrarFoto); // para mostrarlo en el overlay


    // METERLO EN EL BODY DE HTLM siembre creando una variable
    const body = document.querySelector('body'); // creamos una variables body para que se nos agrege en nuestro html
    body.appendChild(overlay);  // para mostrarlo en el proyecto
    body.classList.add('fijar-body');
    
};
